* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}

.container {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.w-100 {
    width: 100%;
}

[class*=col-] {
    padding: 0 15px;
}

.col-1 {
    width: 8.33%;
}

.col-2 {
    width: 16.66%;
}

.col-3 {
    width: 25%;
}

.col-4 {
    width: 33.33%;
}

.col-5 {
    width: 41.66%;
}

.col-6 {
    width: 50%;
}

.col-7 {
    width: 58.33%;
}

.col-8 {
    width: 66.66%;
}

.col-9 {
    width: 75%;
}

.col-10 {
    width: 83.33%;
}

.col-11 {
    width: 91.66%;
}

.col-12 {
    width: 100%;
}


.a-hover {
    &:hover {
        color: #F52225;
    }

    &:after {
        content: "";
        position: absolute;
        background-color: #F52225;
        height: 3px;
        width: 0%;
        left: 0;
        bottom: -8px;
        transition: 0.3s;

    }

    &:hover:after {
        width: 100%;
    }
}

.container {
    .row {
        .col-8 {

            .ab-part1 {
                margin-top: 120px;

                p {
                    &:nth-child(1) {

                        color: #F52225;
                        font-size: 16px;
                        position: relative;

                        &::after {
                            content: "";
                            position: absolute;
                            width: 40px;
                            height: 1px;
                            background-color: #F52225;
                            top: 12px;
                            left: 85px;
                        }
                    }

                    &:nth-child(2) {
                        margin-top: 30px;
                        font-size: 55px;
                        font-weight: 700;
                        color: white;
                    }

                    &:nth-child(3) {
                        font-size: 18px;
                        color: #a9afc3;
                    }
                }
            }

        }
    }

    .row.row-2 {
        padding-top: 60px;

        .col-6 {

            .ab-img {

                img {
                    // width: 600px;
                    // height: 600px;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }

        .col-6.ab-letter {
            position: relative;


            p {
                &:nth-child(1) {
                    font-size: 32px;
                    color: white;
                    margin-bottom: 25px;
                }

                &:nth-child(2) {
                    font-size: 18px;
                    color: white;
                    margin-bottom: 35px;

                    span {
                        color: #F52225;
                    }
                }

                &:nth-child(3) {
                    font-size: 18px;
                    color: #a9afc3;

                    &::after {
                        content: "";
                        position: absolute;
                        width: 95%;
                        height: 0.5px;
                        background-color: #a9afc3;
                        top: 40%;
                        left: 16px;
                    }
                }


            }

            .col-12.personal {

                margin-top: 40px;

                .info {
                    font-size: 20px;
                    font-weight: 600;
                }


                .col-6 {
                    ul {
                        li {
                            margin-top: 10px;

                            color: #a9afc3;
                            font-size: 15px;

                            span {
                                color: white;
                                margin-left: 2px;
                            }
                        }
                    }
                }


            }

            button {
                position: absolute;
                bottom: 20px;
                width: 180px;
                height: 50px;
                background-color: transparent;
                border: 1px solid white;
                border-radius: 10px;

                span {
                    color: white;
                    font-size: 18px;
                }

            }
        }
    }
}







