//Progress Bar

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99999;

}

.progress-container {
    background-color: rgba(150, 150, 150, 0.6);
    height: 10px;
    width: 100%;

  
}

.progress-bar {
    background-color: #F52225;
    height: 10px;
    width: 0%;
}




//Progress Bar

.goAbout {
    position: absolute;
    bottom: 18px;
    width: 60px;
    height: 80px;
    left: 48%;
}


.top {
    position: fixed;
    bottom: 15px;
    right: 15px;
    line-height: 50px;
    background-color: #F52225;
    padding: 0 20px;
    z-index: 9999;
    border-radius: 50%;

    i {
        color: white;
    }
}




.a-hover {
    &:hover {
        color: #F52225;
    }

    &:after {
        content: "";
        position: absolute;
        background-color: #F52225;
        height: 3px;
        width: 0%;
        left: 0;
        bottom: -8px;
        transition: 0.3s;

    }

    &:hover:after {
        width: 100%;
    }

}







// Navbar

.navbar {
    background-color: #040809;
    width: 100%;
    height: 84px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 50px;
    position: fixed;
    // position: sticky;
    top: 0px;
    z-index: 999;

    .logo {
        h1 {
            color: white;
            font-size: 28px;
            letter-spacing: 1px;

            span {
                color: #F52225;
                font-size: 45px;
            }
        }
    }

    .menu {

        display: flex;
        align-items: center;
        justify-content: center;

        ul {
            display: flex;
            align-items: center;
            gap: 20px;

            li {

                a {
                    color: white;
                    position: relative;
                }
            }
        }
    }

}


.navBars {
    position: relative;
    padding: 10px;

    span {
        background-color: white;
        width: 47px;
        height: 3px;
        display: block;
        transition: 0.5s;

        &:nth-child(2) {
            margin: 9px 0px;
        }

    }

}

.navBars.change {

    span {
        background-color: #F52225;

        &:nth-child(2) {
            opacity: 0;

        }

        &:nth-child(1) {
            transform: rotate(45deg);
            transition: 0.5s;
            position: absolute;
            margin-top: 20px;

        }

        &:nth-child(3) {
            transform: rotate(-45deg);
            transition: 0.5s;
            margin-bottom: 20px;
        }

    }


}

.navBars {
    display: none;
}




@media screen and (max-width: 600px) {

    .navbar {
        padding: 0 1rem;


        .navBars {
            display: block;
        }

        // .logo {
        //     padding: 0 10px;
        // }

        .menu {
            display: block;
            position: absolute;
            top: 80px;
            height: calc(100vh - 84px);
            width: 100%;
            background-color: #040809;
            padding: 0 10px;
            // left: -100%;
            left: 0%;
            transition: 0.5s;
            z-index: 9999;

            ul {
                display: block;

                li {
                    &:nth-child(n+2) {
                        margin-top: 30px;
                    }

                }
            }
        }

        .length {
            left: -100%;
        }
    }





}


@media screen and (min-width: 600px) and (max-width:992px) {
    .navbar {
        padding: 0 1rem;
    }
}






////////Home//
.home {
    background-color: #040809;
    // height: calc(100vh - 84px);
    height: 100vh;
    position: relative;


    .container {
        .row {

            .col-7 {
                padding-top: 120px;

                .home-letter1 {

                    p {
                        font-size: 26px;
                        margin-bottom: 20px;
                        color: white;
                    }
                }

                .home-letter2 {
                    p {

                        font-size: 69px;
                        color: #F52225;
                        font-weight: 700;

                        &:nth-child(1) {
                            color: white;
                            border-right: solid 3px #F52225;
                            white-space: nowrap;
                            overflow: hidden;

                            animation: animated-text 4s steps(29, end) 1s 1 normal both, infinite,
                                animated-cursor 600ms steps(29, end) infinite,
                                slide 1.5s steps(10) infinite;
                        }

                        @keyframes animated-text {
                            from {
                                width: 0;
                            }

                            to {
                                width: 100%;
                            }

                        }

                        @keyframes animated-cursor {
                            from {
                                border-right-color: #F52225
                            }

                            to {
                                border-right-color: transparent;
                            }
                        }

                        @keyframes slide {
                            100% {
                                top: -360px
                            }

                        }
                    }



                }

                .home-letter3 {
                    color: rgb(158, 156, 156);
                }

                .home-button {
                    button {

                        margin-top: 20px;
                        width: 170px;
                        height: 50px;
                        border-radius: 5px;
                        outline: none;
                        background: transparent;
                        border: 1px solid white;

                        p {
                            position: relative;
                            font-size: 18px;
                            color: white;
                        }
                    }
                }

            }
        }

    }

    .home-img.col-5 {
        display: flex;
        position: relative;
        justify-content: space-between;


        .home-image {

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
                z-index: -3;
            }
        }

        .social-media {
            position: absolute;
            z-index: 99;
            color: white;
            display: flex;
            flex-direction: column;
            right: 0;
            bottom: 0;

            a {
                color: white;
                font-size: 24px;
            }
        }

    }

    .bottom-ani {
        position: absolute;
        bottom: 50px;
        left: 50%;
        height: 30px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;

        span {
            display: block;
            width: 20px;
            height: 20px;
            border-bottom: 3px solid #F52225;
            transform: rotate(45deg);
            border-right: 3px solid #F52225;
            margin: -10px;
            animation: scroll 2s infinite;

            &:nth-child(2) {
                animation-delay: -2s;

            }

            &:nth-child(3) {
                animation-delay: -4s
            }

            @keyframes scroll {
                0% {
                    opacity: 0;
                    transform: rotate(45deg) translate(-20px, -20px);
                }

                50% {
                    opacity: 1;
                }

                100% {
                    opacity: 0;
                    transform: rotate(45deg) translate(20px, 20px);
                }

            }

        }


    }
}

